import React from 'react'
import pr1 from "../images/1.JPG";
import pr2 from "../images/2.JPG";
import pr3 from "../images/Sebitse.JPG";
import pr4 from "../images/Thalone.JPG";
import portfolio from "../images/portfolio.jpg";
import CV from "../images/CV.pdf"
//Font Awesome imports
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSearchPlus } from '@fortawesome/free-solid-svg-icons';
//React popup box
import "react-popupbox/dist/react-popupbox.css";
import {PopupboxManager, PopupboxContainer} from "react-popupbox"



function Portfolio() {

    //Project 1: MyHope
    const openPopupboxMyHope = () => {
        const content =(
        <>
        <img className="portfolio-image-popupbox" src={pr1} alt="MyHope Web Portal" />
        <p> This is a web portal designed to make training staff easier and more accessible. It was developed using WordPress, WAMP Server, and Windows Remote Desktop Connection.</p>
        <b>Demo</b> <a className="hyper-link" onClick={() => window.open("http://training.myhope.org.za", "_blank")} >Click Here to view the website</a>
    </>
        )
        PopupboxManager.open({content});
    }

    const popupConfigMyHope = {
           titleBar: {
               enable: true,
               text: "MyHope Web Portal"
           },
           fadeIn: true,
           fadeInSpeed: 500
    }

    //Project 2: Application For Leave
    const openPopupboxApp4Leave = () => {
        const content =(
        <>
        <img className="portfolio-image-popupbox" src={pr2} alt="App4Leave Project" />
        <p> This is an application designed to be used by HR departments within companies to book leave for employees. It was designed using AngularJS, and a .Net Web API</p>
        <b>GitHub</b> <a className="hyper-link" onClick={() => window.open("https://github.com/AllThatChocolate/App4Leave")} >View this code first project on GitHub</a>
    </>
        )
        PopupboxManager.open({content});
    }

    const popupConfigApp4Leave = {
           titleBar: {
               enable: true,
               text: "Applicatiion For Leave"
           },
           fadeIn: true,
           fadeInSpeed: 500
    }

    //Project 3: Portfolio
    // const openPopupboxPortfolio = () => {
    //     const content =(
    //     <>
    //     <img className="portfolio-image-popupbox" src={portfolio} alt="Portfolio" />
    //     <p> This portfolio was designed using JavaScript (ReactJS, ParticlesJS, EmailJS) and Bootstrap while being hosted on GitHub</p>
    //     <b>Demo</b> <a className="hyper-link" onClick={() => window.open("https://allthatchocolate.github.io/Portfolio/", "_blank")} >This Link will reload the website</a>
    // </>
    //     )
    //     PopupboxManager.open({content});
    // }

    // const popupConfigPortfolio = {
    //        titleBar: {
    //            enable: true,
    //            text: "Portfolio"
    //        },
    //        fadeIn: true,
    //        fadeInSpeed: 500
    // }

    //Project 4: Sebitse.com
    const openPopupboxSebitse = () => {
        const content =(
        <>
            <img className="portfolio-image-popupbox" src={pr3} alt="Sebitse" />
            <p> A Real Estate website designed using HTML, PHP, JavaScript, and MySQL hosted on KonsoleH</p>
            <b>Demo</b> <a className="hyper-link" onClick={() => window.open("https://sebitse.co.za/", "_blank")} >View the sebitse.com page</a>
        </>
        )
        PopupboxManager.open({content});
    }

    const popupConfigSebitse = {
           titleBar: {
               enable: true,
               text: "Sebitse.co.za"
           },
           fadeIn: true,
           fadeInSpeed: 500
    }

      //Project 5: THAL One
      const openPopupboxThal = () => {
        const content =(
        <>
        <img className="portfolio-image-popupbox" src={pr4} alt="ToDo" />
        <p> A code-first ToDo List Application with a React-Native FrontEnd and a Laravel Backend hosted on GitHub</p>
        <b>Demo</b> <a className="hyper-link" onClick={() => window.open("https://thalone.co.za/", "_blank")} >Click here to view the website</a>
    </>
        )
        PopupboxManager.open({content});
    }

    
    const popupConfigThal = {
           titleBar: {
               enable: true,
               text: "Thal One"
           },
           fadeIn: true,
           fadeInSpeed: 500
    }

    

    return (
            <div id="portfolio" className="portfolio wrapper">
                <div className="container">
                    <h1 className="text-uppercase text-center py-5">Portfolio</h1>
                    <div className="image-box-wrapper row justify-content-center">
                        <div className="portfolio-image-box col-md-3" onClick={openPopupboxMyHope}> 
                            <img className="portfolio-image" src={pr1} alt="MyHope Web Portal WordPress Project" />
                            <div className="overflow"></div>
                            <FontAwesomeIcon className="portfolio-icon" icon={faSearchPlus} />
                        </div>

                        <div className="portfolio-image-box col-md-3" onClick={openPopupboxApp4Leave}>
                            <img className="portfolio-image" src={pr2} alt="Application For Leave Angular.JS .Net Web API Project" />
                            <div className="overflow"></div>
                            <FontAwesomeIcon className="portfolio-icon" icon={faSearchPlus} />
                        </div>

                    {/**      
                        <div className="portfolio-image-box col-md-3" onClick={openPopupboxPortfolio}>
                            <img className="portfolio-image" src={portfolio} alt="Portfolio ReactJS and Bootstrap 4 Project" />
                            <div className="overflow"></div>
                            <FontAwesomeIcon className="portfolio-icon" icon={faSearchPlus} />
                        </div>

                        
                         
                        */} 
                        <div className="portfolio-image-box col-md-3" onClick={openPopupboxThal}>
                            <img className="portfolio-image" src={pr3} alt="React-Native and Laravel project" />
                            <div className="overflow"></div>
                            <FontAwesomeIcon className="portfolio-icon" icon={faSearchPlus} />
                        </div>
                        <div className="portfolio-image-box col-md-3" onClick={openPopupboxSebitse}>
                            <img className="portfolio-image" src={pr4} alt="Real Estate Website" />
                            <div className="overflow"></div>
                            <FontAwesomeIcon className="portfolio-icon" icon={faSearchPlus} />
                        </div> 

                        <div className="portfolio-image-box col-md-12  text-center">
                            <a href={CV} className="text-center portfolio-image-box mx-auto btn" target="_blank">
                                    <h3>Click Here to view my CV</h3>
                            </a>
                        </div> 
                        
                    </div>
                </div>
                <PopupboxContainer {...popupConfigMyHope} />
                <PopupboxContainer {...popupConfigApp4Leave} />
                {/* <PopupboxContainer {...popupConfigPortfolio} /> */}
                <PopupboxContainer {...popupConfigSebitse} />
                <PopupboxContainer {...popupConfigThal} /> 
            </div>
        
    )
}

export default Portfolio
